@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
}


.world-beers-page{display:flex;flex-direction:column}.image{display:flex;margin-top:40px;height:500px;width:100%;border-radius:8px;opacity:0.8}@media only screen and (max-width: 600px){.image{height:300px;width:90%;margin:auto auto}}@media only screen and (max-width: 1005px){.image{height:400px;width:90%;margin:auto auto}}.title{font-size:40px;justify-content:center;letter-spacing:3px;font-family:Fantasy, Arial, Times, Serif;margin-top:40px;color:#000;display:flex}.change-button{display:flex;margin-top:15px;background-color:#4caf50;margin-left:auto;margin-right:auto;border:none;padding:10px 20px;text-decoration:none;cursor:pointer;color:#fff;font-size:14px;text-align:center;outline:0;border-radius:8px;cursor:pointer}.change-button:hover{opacity:0.6}

.beer-card{display:flex;margin-bottom:30px;margin-left:50px;width:80%;min-height:400px;flex-direction:row}@media only screen and (max-width: 600px){.beer-card{flex-direction:column}}.beer-text{text-align:justify;width:100%;flex-direction:column;margin-top:auto;margin-left:30px;margin-bottom:auto}@media only screen and (max-width: 600px){.beer-text{margin-top:-10px}}.text{font-size:1rem;color:#000}.beer-img{height:400px;display:flex;width:200px}@media only screen and (max-width: 600px){.beer-img{margin-bottom:-70px;align-self:center}}.img{width:100%;object-fit:contain;height:100%}@media only screen and (max-width: 600px){.img{height:70%;width:60%;margin-left:auto;margin-right:auto}}.no-photo{margin-top:180px;display:flex;margin-right:80px;font-size:2rem}strong{color:maroon}

.spinner{border:16px solid gray;border-top:16px solid green;border-radius:50%;width:100px;height:100px;animation:spin 2s linear infinite;margin-right:auto;margin-left:auto;margin-top:100px}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

.icon-position-favourite-button{display:flex;justify-content:flex-end;width:80%}@media only screen and (max-width: 600px){.icon-position-favourite-button{bottom:-250px;position:relative}}.icon{color:#9acd32;cursor:pointer;font-size:1.75rem;margin-top:30px;bottom:-70px;position:relative}.icon:hover{opacity:0.6}.spinner-anim{animation:circle 500ms linear}@keyframes circle{0%{transform:rotate(0deg);font-size:40px}100%{transform:rotate(360deg);font-size:2, 3rem}}

.beer-day-page{display:flex;flex-direction:column}.title-beer-day{font-size:40px;justify-content:center;letter-spacing:3px;font-family:Fantasy, Arial, Times, Serif;margin-top:40px;color:#000;display:flex}.button-choose{margin-top:40px;color:#fff;width:100px;height:100px;border-radius:50%;outline:0;border:none;background-color:#4caf50;font-size:15px;cursor:pointer;display:flex;align-items:center;margin-bottom:30px;align-self:center}.button-choose:hover{background-color:#3e8e41}.random-beer{margin-left:150px;animation:showInFromNone 1s linear}@media only screen and (max-width: 600px){.random-beer{margin-left:20px}}

.title-favourite-beers{font-size:40px;justify-content:center;letter-spacing:3px;font-family:Fantasy, Arial, Times, Serif;margin-top:40px;color:#000;display:flex}.favourite-beer{margin-left:70px;margin-right:90px}@media only screen and (max-width: 600px){.favourite-beer{margin-left:20px}}.line{border-bottom:2px solid gray;margin-left:20px;margin-right:20px;opacity:0.4}.opacity-anim{animation:fadeInFromNone 1500ms linear normal}@keyframes fadeInFromNone{0%{opacity:1}100%{opacity:0}}

.icon-position-delete-button{display:flex;justify-content:flex-end;width:80%}@media only screen and (max-width: 600px){.icon-position-delete-button{left:50px;position:relative}}.delete-beer{border:none;outline:0;color:red;font-size:1.7rem;cursor:pointer;background-color:#f5f5f5;margin-top:50px;animation:showInFromNone 2s linear}.delete-beer:hover{opacity:0.5}@keyframes showInFromNone{0%{opacity:0}100%{opacity:0.9}}

.search-page{display:flex;flex-direction:column;width:100%}@media only screen and (max-width: 600px){.search-page{margin-left:20px;width:95%}}.title-search{font-size:40px;justify-content:center;letter-spacing:3px;font-family:Fantasy, Arial, Times, Serif;margin-top:40px;color:#000;display:flex}.button-find-beers{margin-top:-150px;color:#fff;width:100px;height:100px;border-radius:50%;outline:0;border:none;background-color:#4caf50;font-size:15px;cursor:pointer;display:flex;align-items:center;justify-content:center;align-self:flex-end}.button-find-beers:hover{background-color:#3e8e41}@media only screen and (max-width: 600px){.button-find-beers{align-self:flex-start;margin-top:20px}}.input{width:220px;height:10px;padding:15px 10px;font-size:1rem;border:2px solid green;outline:0;color:#000;margin-left:10px;border-radius:8px}.select{margin-left:10px;margin-right:10px;border-radius:8px;outline:0;margin-bottom:20px;width:200px;height:35px;cursor:pointer;background-color:#f5f5f5;border:2px solid gray}.errors{color:red;font-size:0.75rem}.form{display:flex;flex-direction:row;margin-left:230px;margin-top:-25px}.search-beers-button{background-color:#4caf50;border:none;padding:10px 20px;text-decoration:none;cursor:pointer;color:#fff;font-size:14px;text-align:center;outline:0;border-radius:8px;cursor:pointer;margin-top:-10px;margin-left:30px}.search-beers-button:hover{background-color:#3e8e41}.previous-button{background-color:red;width:170px;border:none;padding:10px 20px;text-decoration:none;cursor:pointer;color:#fff;font-size:14px;text-align:center;outline:0;border-radius:8px;cursor:pointer}.previous-button:hover{background-color:darkred}.next-button{background-color:#4caf50;border:none;padding:10px 20px;text-decoration:none;cursor:pointer;color:#fff;font-size:14px;text-align:center;outline:0;border-radius:8px;cursor:pointer;margin-right:50px;width:170px}.next-button:hover{background-color:#3e8e41}.navigation-page-buttons{display:flex;width:1000px;justify-content:space-around;margin-bottom:40px;margin-top:50px;cursor:pointer}@media only screen and (max-width: 600px){.navigation-page-buttons{width:450px;margin-left:auto;margin-right:auto}}.beers-per-page{width:40px;border:2px solid gray;outline:0;display:flex;align-self:flex-end}@media only screen and (max-width: 600px){.beers-per-page{align-self:flex-start}}.button-clean-filter{margin-left:10px;z-index:1;border:none;padding:10px 20px;text-decoration:none;cursor:pointer;color:#fff;font-size:14px;text-align:center;outline:0;border-radius:8px;cursor:pointer;width:150px;background-color:#4caf50}.button-clean-filter:hover{opacity:.6}



.nav-link{background-color:gray;width:25%;display:flex;align-items:center;justify-content:center;cursor:pointer}.nav-link:hover{background-color:#d3d3d3}.links-button{background-color:inherit;text-align:center;color:#fff;outline:0;font-size:1rem;border:none;display:block;cursor:pointer}.nav{list-style-type:none;margin:0;padding:0;overflow:hidden;position:-webkit-sticky;position:sticky;top:0;display:flex;width:100%;height:60px;z-index:2}

.counter{font-size:1rem;color:white}

body{background-color:#f5f5f5;max-width:1000px;box-sizing:border-box;margin:0 auto;font-family:'Roboto';font-size:20px}@media only screen and (max-width: 600px){body{font-size:10px}}

